import axios from "../../axios/request"
import store from '../index'

export default {
    namespaced:true,
    state(){
        return{
            requests:[]
        }
    },
    mutations:{
        setRequests(state,requests){
            state.requests = requests
        },
        addRequests(state,requests){
            state.requests.push(requests)
        }
    },
    actions:{
        async create({commit,dispatch}, payload){
            try{
                const token = store.getters['auth/token']
                const {data} = await axios.post(`/requests.json?auth=${token}`, payload)
                commit('addRequests',{...payload,id:data.name})
                dispatch('setMessage',{
                    value:'Ariza muvofaqiyatli  qoshildi',
                    type:'primary'
                },{root:true})
            }
            catch(e){
                dispatch('setMessage',{
                    value:e.message,
                    type:'danger'
                }, {root:true})
            }
        },
        async load({commit,dispatch}){
            try{
                const token = store.getters['auth/token']
                const {data} = await axios.get(`/requests.json?auth=${token}`)
                console.log(data)
                const requests = Object.keys(data).map(id=>({...data[id], id}))
                commit('setRequests', requests)
            }
            catch(e){
                dispatch('setMessage',{
                    value:e.message,
                    type:'danger'
                })
            }
        },
        async loadById({dispatch},id){
            try{
                const token = store.getters['auth/token']
                const {data} = await axios.get(`/requests/${id}.json?auth=${token}`)
                return data
            }
            catch(e){
                dispatch('setMessage',{
                    value:e.message,
                    type:'danger'
                })
            }
        },
        async remove({dispatch},id){
            try{
                const token = store.getters['auth/token']
                await axios.delete(`/requests/${id}.json?auth=${token}`)
                dispatch('setMessage',{
                    value:'Ariza muvofaqiyatli o`chirildi',
                    type:'primary'
                },{root:true})
            }
            catch(e){
                dispatch('setMessage',{
                    value:e.message,
                    type:'danger'
                })
            }
        },
        async update({dispatch},request ){
            try{
                const token = store.getters['auth/token']
                await axios.put(`/requests/${request.id}.json?auth=${token}`,request)
                dispatch('setMessage',{
                    value:'Ariza muvofaqiyatli o`zgartirildi',
                    type:'primary'
                },{root:true})
            }
            catch(e){
                dispatch('setMessage',{
                    value:e.message,
                    type:'danger'
                })
            }
        }

    },
    getters:{
        requests(state){
            return state.requests
        }
    }
}