const formatter = new Intl.NumberFormat('uz',{style:'currency', currency:'UZS'})
// const formatter = new Intl.NumberFormat('en-UZ',{style:'currency', currency:'USD'})
// const formatter = new Intl.NumberFormat('eu',{style:'currency', currency:'EUR'})

export function currency(value){
    return formatter.format(value)
}