import { createStore, createLogger} from 'vuex'
import auth from './Modules/auth-modules'
import request from './Modules/request.moudle'


const plugins = []

if(process.env.NODE_END === 'development'){
  plugins.push(createLogger)
}
 
  export default createStore({
    plugins,
      state(){
        return{
          message:null,
          sidebar:null
        }
      },
      mutations: {
        setMessage(state,message){
          state.message = message
        },
        clearMessage(state){
          state.message = null
        },
        openSidebar(state){
          state.sidebar = true
        },
        closeSidebar(state){
          state.sidebar = false
        }

      },
      actions:{
        setMessage({commit},message){
          commit('setMessage',message)  
          setTimeout(()=>{
            commit('clearMessage')
          },5000)
        }
      },
      modules: {
        auth,
        request

      }
 })
