<template>
  <div class="sidebar" v-if="sidebar">
    <span class="sidebar-close" @click="close">&times;</span>
    <div class="sidebar-content">
      <p>
        Lorem ipsum dolor sit amet, consectetur adipisicing elit. Consequatur dolores eius odio possimus repellendus. Ad alias aperiam aut dolorem, ducimus fugit, ipsa nihil officia praesentium quam quas quia quidem sint?
      </p>
    </div>
  </div>
</template>

<script>
import {computed} from 'vue'
import {useStore} from 'vuex'
export default {
  setup(){
    const store = useStore()

    const  sidebar = computed(()=>store.state.sidebar)
    return{
      sidebar,
      close:()=>{store.commit('closeSidebar')}
    }
  }

}
</script>

