<template>
    <h4 v-if="requests.length===0" class="text-center">Arizalar yoq</h4>
    <table v-else class="table">
        <thead>
          <tr>
              <th>#</th>
              <th>FISH</th>
              <th>Telefon</th>
              <th>Summa</th>
              <th>Status</th>
              <th>Amallar</th>
          </tr>
        </thead>
        <tbody>
            <tr v-for="(r, idx) in requests" :key="r.id">
                <td>{{idx+1}}</td>
                <td>{{r.fish}}</td>
                <td>{{r.phone}}</td>
                <td>{{currency(r.sum)}}</td>
                <td>
                  <app-status :type="r.status"></app-status>
                </td>
                <td>
                  <router-link v-slot="navigate" :to="{name:'Request',params:{id:r.id}}">
                    <button class="btn" @click="navigator">Ochish</button>
                  </router-link>
                </td>
            </tr>
        </tbody>
    </table>
</template>
<script>
import {currency} from "@/utils/Currency";
import AppStatus from "@/components/ui/AppStatus";

export default {
    props:['requests'],
    setup(){
      return{
        currency
      }
    },
  components:{AppStatus}
}
</script>