<template>
  <div class="filter">
    <div class="form-control">
      <input type="text" placeholder="FISH ni kiriting.." v-model="name">
    </div>
    <div class="form-control">
      <select v-model="status">
        <option disabled selected>Tanlang...</option>
        <option value="done">Bajarildi</option>
        <option value="cancelled">Qaytarildi</option>
        <option value="active">Tasdiqlanmoqda</option>
        <option value="sending">Yuborilmoqda</option>
      </select>
    </div>
    <button class="btn warning" v-if="isActive" @click="reset">Filterni tozalash</button>
  </div>
</template>

<script>
import {ref,watch,computed} from 'vue'
export default {
  emits:['update:modelValue'],
  props:['modelValue'],
  setup(_,{emit}){
    const name = ref()
    const status = ref()

    const isActive = computed(()=> name.value || status.value)

    watch([name,status],values => {
      emit('update:modelValue',{
        name:values[0],
        status:values[1]
      })
    })

    return {
      name,
      status,
      isActive,
      reset:()=>{
        name.value = '',
        status.value = null
      }
    }

  }

}
</script>

