<template>
  <app-loader v-if="loading"> </app-loader>
  <app-page title="Arizalar ro'yxati" v-else>

    <template #header>
      <button class="btn primary" @click="modal=true">Yuborish</button>
    </template>
    <request-filter v-model="filter"></request-filter>
    <request-table :requests="requests"></request-table>

    <teleport to='body'>
      <app-modal v-if="modal" @close="modal=false" title="Arizalar qoshish">
        <request-modal @created="modal=false"></request-modal>
      </app-modal>
    </teleport>
  </app-page>
</template>

<script>
import {ref,computed, onMounted} from 'vue'
import AppPage from '../components/ui/AppPage.vue'
import RequestTable from '../components/request/RequestTable.vue'
import AppModal from '../components/ui/AppModal.vue'
import RequestModal from '../components/request/RequestModal.vue'
import AppLoader from "@/components/ui/AppLoader";
import RequestFilter from "@/components/request/RequestFilter";
import {useStore} from 'vuex'
export default {
  setup(){
    const store = useStore()
    const modal = ref(false)
    const loading = ref(false)
    const filter = ref({})
    const requests = computed(()=>store.getters['request/requests']
        .filter(request => {
          if(filter.value.name){
            return request.fish.includes(filter.value.name)
          }
          return request
        })
        .filter(request => {
          if(filter.value.status){
            return filter.value.status === request.status
          }
          return request
        })
     )


    onMounted(async ()=>{
      loading.value = true
      await store.dispatch('request/load')
      loading.value = false
    })

    return{
      modal,
      requests,
      loading,
      filter
    }
  },
  components:{AppPage,RequestTable,AppModal,RequestModal,AppLoader,RequestFilter}
}
</script>
