import {useField, useForm} from "vee-validate";
import * as yup from "yup";
import {useStore} from "vuex";
import {useRouter} from "vue-router";

export function useRequestForm(fn){
    const store = useStore()
    const router = useRouter()
    const {handleSubmit,isSubmitting,submitCount} = useForm({
        initialValues:{
            status:'active'
        }
    })
    const {value:fish, errorMessage:fError, handleBlur:fBlur} = useField('fish',
        yup
            .string()
            .required('Fish to\'ldirilishi shart')
            .trim()
    )
    const {value:phone, errorMessage:pError, handleBlur:pBlur} = useField('phone',
        yup
            .string()
            .trim()
            .required('Telefon raqam to\'ldirilishi shart')
            .min(9,'kamida 9 ta belgi bo\'lishi shart')
            .max(19,'ko\'pi bilan 19 belgi bo\'lishi shart')
    )
    const {value:sum, errorMessage:sError, handleBlur:sBlur} = useField('sum',
        yup
            .number()
            .required('Summa to\'ldirilishi shart')
            .min(0,'0 dan kom bo\'lmasligi shart')
    )
    const {value:status} = useField('status')

    const Submit = handleSubmit(fn)

    return {
        fish,
        fError,
        fBlur,
        phone,
        pError,
        pBlur,
        sum,
        sError,
        sBlur,
        status,
        Submit,
        isSubmitting,
    }
}